import React, { useState } from 'react';
import { useSelector } from 'react-redux';
import { Link } from 'react-router-dom';

// Картинки
import logo_nav from '../img/nav_logo.svg';

import dark_logo_nav from '../img/nav_logo_dark.svg';
// Конец картинок

// Иконки
// Конец иконок

function HeaderNav({ header }) { 
  let theme = localStorage.getItem('theme') === 'light';

  let isAuth = true;
  const user = useSelector((state) => state.userReducer);
  isAuth = user.isAuth;
  const [isOpenMenu, setIsOpenMenu] = useState(false);

  function openMenu() {
    setIsOpenMenu(true);
  }
  function closeMenu() {
    setIsOpenMenu(false);
  }

  return (
    <>
      {/* // <!-- Адаптив меню --> */}
      <div className="header_bg_hamburger_menu" style={isOpenMenu ? { right: 0 } : null}>
        <ul className="header_hamburger_menu">
          <li className="header_hamburger_menu_title">
            <h2>МЕНЮ</h2>
            <svg
              onClick={closeMenu}
              id="header_hamburger_menu_btn_close"
              width="25"
              height="25"
              viewBox="0 0 25 25"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <rect width="25" height="25" rx="3" fill="white" />
              <path
                d="M6.48435 5.01165C6.28789 4.8219 6.02476 4.7169 5.75164 4.71928C5.47852 4.72165 5.21726 4.8312 5.02412 5.02434C4.83099 5.21747 4.72144 5.47873 4.71906 5.75185C4.71669 6.02497 4.82168 6.2881 5.01143 6.48456L11.0271 12.5002L5.01039 18.5158C4.9109 18.6119 4.83154 18.7268 4.77695 18.8539C4.72236 18.981 4.69362 19.1177 4.69242 19.256C4.69122 19.3943 4.71758 19.5315 4.76995 19.6595C4.82233 19.7875 4.89967 19.9038 4.99748 20.0016C5.09528 20.0994 5.21159 20.1768 5.33961 20.2292C5.46762 20.2815 5.60479 20.3079 5.7431 20.3067C5.88141 20.3055 6.0181 20.2768 6.14519 20.2222C6.27227 20.1676 6.38722 20.0882 6.48331 19.9887L12.5 13.9731L18.5156 19.9887C18.7121 20.1785 18.9752 20.2835 19.2483 20.2811C19.5214 20.2787 19.7827 20.1692 19.9758 19.976C20.169 19.7829 20.2785 19.5216 20.2809 19.2485C20.2833 18.9754 20.1783 18.7123 19.9885 18.5158L13.9729 12.5002L19.9885 6.48456C20.1783 6.2881 20.2833 6.02497 20.2809 5.75185C20.2785 5.47873 20.169 5.21747 19.9758 5.02434C19.7827 4.8312 19.5214 4.72165 19.2483 4.71928C18.9752 4.7169 18.7121 4.8219 18.5156 5.01165L12.5 11.0273L6.48435 5.0106V5.01165Z"
                fill="url(#paint0_linear_554:13)"
              />
              <defs>
                <linearGradient
                  id="paint0_linear_554:13"
                  x1="4.69238"
                  y1="12.513"
                  x2="20.2809"
                  y2="12.513"
                  gradientUnits="userSpaceOnUse"
                >
                  <stop stopColor="#F72585" />
                  <stop offset="1" stopColor="#B5179E" />
                </linearGradient>
              </defs>
            </svg>
          </li>
          <li className="header_hamburger_menu_item">Главная</li>
          <li className="header_hamburger_menu_item">Новости</li>
          <li className="header_hamburger_menu_item">TRichest</li>
          {isAuth ? (
            <>
              <Link to="/profile">
                <li className="header_hamburger_menu_item">Профиль</li>
              </Link>
            </>
          ) : (
            <Link to="/login">
              <li className="header_hamburger_menu_item">Авторизация</li>
            </Link>
          )}
        </ul>
      </div>
      {/* // <!-- Конец адаптив. меню --> */}
      <div className={`header_nav ${header === 1 ? '' : 'container'}`}>
        <div className="header_nav_elements">
          <div className="header_logo">
            <img
              src={theme ? logo_nav : dark_logo_nav}
              alt="logotype"
              className="header_logo_img"
            />
          </div>
          <ul className="header_menu">
            <li className="header_menu_item">
              <Link to="/" className="header_menu_link">
                Главная
              </Link>
            </li>
            <li className="header_menu_item">
              <a rel="noreferrer" href="http://localhost:3000" className="header_menu_link">
                Новости
              </a>
            </li>
            <li className="header_menu_item">
              <a rel="noreferrer" href="http://localhost:3000" className="header_menu_link">
                TRichest
              </a>
            </li>
          </ul>
          {/* <!-- Конец меню  --> */}

          <div className="header_btns">
            {isAuth ? (
              <>
                <Link to="/profile" className="nav_btn_auth nav_btn_user">
                  <svg
                    width="24"
                    height="24"
                    viewBox="0 0 24 24"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M12 2C13.4587 2 14.8576 2.57946 15.8891 3.61091C16.9205 4.64236 17.5 6.04131 17.5 7.5C17.5 8.95869 16.9205 10.3576 15.8891 11.3891C14.8576 12.4205 13.4587 13 12 13C10.5413 13 9.14236 12.4205 8.11091 11.3891C7.07946 10.3576 6.5 8.95869 6.5 7.5C6.5 6.04131 7.07946 4.64236 8.11091 3.61091C9.14236 2.57946 10.5413 2 12 2ZM12 15.75C18.0775 15.75 23 18.2113 23 21.25V24H1V21.25C1 18.2113 5.9225 15.75 12 15.75Z"
                      fill="white"
                    />
                  </svg>
                </Link>
                <Link to="/" className="nav_btn_auth nav_btn_settings">
                  <svg
                    width="24"
                    height="24"
                    viewBox="0 0 24 24"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M14.0638 23H9.9369C9.67833 23 9.4275 22.9143 9.22603 22.7571C9.02456 22.5998 8.88455 22.3805 8.82922 22.1354L8.36777 20.063C7.75221 19.8013 7.16852 19.4741 6.62745 19.0873L4.54473 19.7308C4.29821 19.8071 4.03221 19.7992 3.79091 19.7086C3.54962 19.6179 3.34754 19.45 3.21823 19.2325L1.15025 15.7664C1.0223 15.5487 0.974268 15.2954 1.01402 15.0478C1.05377 14.8002 1.17894 14.5731 1.36906 14.4035L2.98467 12.9735C2.9112 12.3257 2.9112 11.6721 2.98467 11.0243L1.36906 9.5976C1.17867 9.42795 1.05333 9.20058 1.01357 8.95276C0.973815 8.70494 1.02201 8.45137 1.15025 8.2336L3.21369 4.7653C3.343 4.54785 3.54508 4.37985 3.78638 4.28921C4.02768 4.19856 4.29367 4.19073 4.5402 4.267L6.62292 4.9105C6.89955 4.7125 7.18753 4.5277 7.48457 4.3605C7.77142 4.2043 8.06619 4.0624 8.36777 3.9359L8.83035 1.8657C8.88542 1.62062 9.02517 1.40116 9.22643 1.2437C9.42768 1.08625 9.67836 1.00026 9.9369 1H14.0638C14.3223 1.00026 14.573 1.08625 14.7743 1.2437C14.9755 1.40116 15.1153 1.62062 15.1703 1.8657L15.6375 3.937C15.9549 4.0734 16.2656 4.2263 16.566 4.3968C16.846 4.5541 17.117 4.7268 17.3778 4.9127L19.4616 4.2692C19.708 4.19321 19.9737 4.20119 20.2148 4.29182C20.4559 4.38245 20.6577 4.55028 20.787 4.7675L22.8504 8.2358C23.1135 8.6835 23.0228 9.25 22.6316 9.5987L21.016 11.0287C21.0895 11.6765 21.0895 12.3301 21.016 12.9779L22.6316 14.4079C23.0228 14.7577 23.1135 15.3231 22.8504 15.7708L20.787 19.2391C20.6577 19.4566 20.4556 19.6245 20.2143 19.7152C19.973 19.8058 19.707 19.8137 19.4605 19.7374L17.3778 19.0939C16.8371 19.4804 16.2538 19.8073 15.6386 20.0685L15.1703 22.1354C15.1151 22.3803 14.9752 22.5995 14.774 22.7567C14.5727 22.914 14.3222 22.9998 14.0638 23ZM11.9958 7.6C10.793 7.6 9.63954 8.06357 8.78905 8.88873C7.93856 9.71389 7.46077 10.833 7.46077 12C7.46077 13.167 7.93856 14.2861 8.78905 15.1113C9.63954 15.9364 10.793 16.4 11.9958 16.4C13.1986 16.4 14.3521 15.9364 15.2026 15.1113C16.0531 14.2861 16.5309 13.167 16.5309 12C16.5309 10.833 16.0531 9.71389 15.2026 8.88873C14.3521 8.06357 13.1986 7.6 11.9958 7.6Z"
                      fill="white"
                    />
                  </svg>
                </Link>
              </>
            ) : (
              <Link to="/login">
                <button className="header_btn">Личный кабинет</button>
              </Link>
            )}
            <div
              className="header_hamburger"
              onClick={openMenu}
              style={isOpenMenu ? { display: 'none' } : null}
            >
              <span className="header_hamburger_line"></span>
            </div>
          </div>
          {/* <!-- Конец header_btns --> */}
        </div>
      </div>
    </>
  );
}

export default HeaderNav;
