import React from 'react';
// Компоненты
// import Theme from '../../components/chooseTheme/choosetheme';
import HeaderNav from '../../components/HeaderNav';
import Footer from '../../components/Footer';
// import Service from '../../http/service';
import ComponentProfile from './components/profile';
import ComponentSettings from './components/settings';
import ComponentDonate from './components/donate';
// Конец компонентов

import './css/index.css';
import { useState } from 'react';

function Profile() {
  const [activeComponent, setActiveComponent] = useState(1);
  let tmpUserData = { // Якобы данные игрока
    online: true,
    login: 'LOGIN...',
    characters: [
      {
        id: 55,
        name: ''
      }
    ]
  };

  return (
    <>
      <HeaderNav />
      <div className="container">
        <div className="row profile_row">
          <div className="col-12 profile_content">
            <div className="profile_menu">
              <div
                className={
                  activeComponent === 1
                    ? 'profile_menu_item profile_menu_item-active'
                    : 'profile_menu_item'
                }
                onClick={() => setActiveComponent(1)}
              >
                {tmpUserData.login}
                <span className="profile_menu_item-isGame"></span>
              </div>
              <div
                className={
                  activeComponent === 2
                    ? 'profile_menu_item profile_menu_item-active'
                    : 'profile_menu_item'
                }
                onClick={() => setActiveComponent(2)}
              >
                Настройки
              </div>
              <div
                className={
                  activeComponent === 3
                    ? 'profile_menu_item profile_menu_item-active'
                    : 'profile_menu_item'
                }
                onClick={() => setActiveComponent(3)}
              >
                Донат
              </div>
            </div>
            {activeComponent === 1 && <ComponentProfile />}
            {activeComponent === 2 && <ComponentSettings />}
            {activeComponent === 3 && <ComponentDonate />}
          </div>
        </div>
      </div>
      <Footer />
    </>
  );
}

export default Profile;
