import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { useHistory } from 'react-router';

// Компоненты
import HeaderNav from '../components/HeaderNav';
import Footer from '../components/Footer';
import Service from '../http/service';
import { notify_success, notify_warning, notify_error } from '../components/notify';
// Конец компонентов

import './css/login.css';

function Login() {
  const history = useHistory();
  const dispatch = useDispatch();

  const [name, setName] = useState('');
  const [password, setPassword] = useState('');
  function Authlogin() {
    Service.login(name, password)
      .then((res) => {
        console.log(res);
        if (res.data.type === 'success') {
          localStorage.setItem('token', res.data.userData.accessToken);
          dispatch({ type: 'edit_user', payload: res.data.userData.user });
          notify_success(res.data.message);
          history.push('/profile');
        } else {
          return notify_warning(res.data.message);
        }
      })
      .catch((e) => {
        console.log(e);
        return notify_error('Ошибка на стороне сервере');
      });
  }
  return (
    <>
      <HeaderNav />
      <div className="container login_contaier row">
        <div className="login_panel col-12 col-lg-4">
          <div className="login_panel_content ">
            <h3 className="login_panel_content_title">Авторизация</h3>
            <div className="login_panel_content_inputs">
              <input
                className="login_panel_content_input col-8"
                type="text"
                placeholder="Введите логин"
                onChange={(e) => {
                  setName(e.target.value);
                }}
              />
              <input
                className="login_panel_content_input col-8"
                type="password"
                placeholder="Введите пароль"
                onChange={(e) => {
                  setPassword(e.target.value);
                }}
              />
            </div>
            <div className="login_panel_content_btns">
              <button className="login_panel_content_btn col-4" onClick={Authlogin}>
                Войти
              </button>
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </>
  );
}

export default Login;
