import React from 'react';
import HeaderNav from './HeaderNav.jsx';

// Картинки светлой темы
import content_logo from '../img/header_logo.svg';
import pers_light from '../img/pers_light.png';
// Конец картинок светлой темы

// Картинки тёмной темы
import dark_content_logo from '../img/content_logo2.svg';
import dark_fn_pers from '../img/fn-pers2.png';
// Конец картинок тёмной темы

// Иконки светлой темы
import user from '../icon/user.svg';
// Конец иконок светлой темы

// Иконки
import dark_user from '../icon/user_dark.svg';
// import { useSelector } from 'react-redux';
// Конец иконок

function Header({ onlineplayer }) {
  // let theme = useSelector((state) => state.themeReducer.theme) === 'light';
  let theme = localStorage.getItem('theme') === 'light';

  return (
    <>
      {/* // <!-- <span className="nav_bg_shadow"></span> --> */}
      <div className="header">
        <div className="container header_container">
          <HeaderNav header={1} />
          <div className="row">
            <div className="col-12 col-md-7 header_content d-flex align-items-center flex-column">
              <div className="header_content_logo d-none d-md-flex">
                <img src={theme ? content_logo : dark_content_logo} alt="logotype" />
              </div>
              <div className="header_content_slogn">Что-то новое в мире Grand Theft Auto</div>
              <div className="header_content_online d-flex align-items-center justify-content-around">
                <div className="header_content_online_svg">
                  <img src={theme ? user : dark_user} alt="" />
                </div>
                <p className="header_content_online_db">{onlineplayer}</p>
              </div>
              <p className="header_content_text">Сейчас играют</p>
              <a
                href="#playnow"
                className="header_content_button-play d-flex justify-content-around align-items-center"
              >
                Начать играть
                <svg
                  width="50"
                  height="50"
                  viewBox="0 0 50 50"
                  fill="white"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path d="M25 50C18.3223 50 12.0442 47.3995 7.32227 42.6777C2.60049 37.9558 0 31.6777 0 25C0 18.3223 2.60049 12.0442 7.32227 7.32227C12.0442 2.60049 18.3223 0 25 0C31.6777 0 37.9558 2.60049 42.6777 7.32227C47.3995 12.0442 50 18.3223 50 25C50 31.6777 47.3995 37.9558 42.6777 42.6777C37.9558 47.3995 31.6777 50 25 50ZM25 3.90625C13.3688 3.90625 3.90625 13.3688 3.90625 25C3.90625 36.6312 13.3688 46.0938 25 46.0938C36.6312 46.0938 46.0938 36.6312 46.0938 25C46.0938 13.3688 36.6312 3.90625 25 3.90625ZM16.1133 38.1408V11.8592L38.9783 25L16.1133 38.1408ZM20.0195 18.6096V31.3905L31.1389 25L20.0195 18.6096Z" />
                </svg>
              </a>
            </div>
          </div>
          <div className="man d-none d-md-flex">
            <img src={theme ? pers_light : dark_fn_pers} alt="FN pers" />
          </div>
        </div>
      </div>
      {/* <!-- Конец header --> */}
    </>
  );
}

export default Header;
