import { useEffect, useState } from 'react';
import { BrowserRouter as Router, Route, Switch, Redirect } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import Loader from './components/Loader';
import Service from './http/service';

import Main from './main.jsx';
import Login from './pages/login';
import Profile from './pages/profile';

function App() {
  let theme = localStorage.getItem('theme');
  if (!theme || theme !== 'light') {
    theme = 'dark';
    localStorage.setItem('theme', 'dark');
  }
  const dispatch = useDispatch();
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // dispatch({ type: 'SET_THEME', payload: theme });
    Service.auth()
      .then((res) => {
        localStorage.setItem('token', res.data.token);
        dispatch({ type: 'edit_user', payload: res.data.user });
        setLoading(false);
      })
      .catch((e) => {
        console.log(e);
        setLoading(false);
      });
  }, [dispatch, theme]);

  const user = useSelector((state) => state.userReducer);
  // let themeRedux = useSelector((state) => state.themeReducer.theme);

  function ExpressTest() {
    if (loading) {
      return <Loader width="250px" height="250px" />;
    } else {
      if (user.isAuth) {
        return (
          <Switch>
            <Route exact path="/" component={Main} />
            <Route exact path="/profile" component={Profile} />
            <Route exact path="/loader" component={Loader} />

            <Redirect to="/" />
          </Switch>
        );
      } else {
        return (
          <Switch>
            <Route exact path="/" component={Main} />
            <Route exact path="/login" component={Login} />
            <Route exact path="/profile" component={Profile} />
            <Route exact path="/loader" component={Loader} />

            <Redirect to="/" />
          </Switch>
        );
      }
    }
  }

  return (
    <Router>
      <div className={`wrapper ${theme}-theme`}>
        {theme === 'light' ? <div className="top_bg_header"></div> : ''}
        {ExpressTest()}
      </div>
    </Router>
  );
}

export default App;
