import $api from './index';

export default class Service {
  static async login(name, password) {
    return $api.post('/login', { name: name, password: password });
  }
  static async auth() {
    return $api.get('/checkauth');
  }
  static async getLinkQiwi(amount, desc) {
    return $api.post('/getlinkqiwi', { amount, desc });
  }
  static async checkQiwi(billId) {
    return $api.post('/checkqiwi', { billId });
  }
}
